:root {
    --g: rgb(29, 143, 0);
  }

*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-smooth:subpixel-antialiased;
    -webkit-font-smoothing: antialiased;
}
body
{
    font-smooth:subpixel-antialiased;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
    /* font-family: 'Raleway', sans-serif; */
    /* font-family: 'Sora', sans-serif; */
    /* font-family: 'Lato', sans-serif; */
    /* font-family: 'Source Serif 4', sans-serif; */
    /* font-family: 'Noto Serif', serif; */
    font-family: 'Poppins', sans-serif;    
    font-size: 14px;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
    z-index: -1;
}

.loading-plane{
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0);
    z-index: 10;
}
.loading-progress{
    position: absolute;
    bottom: 30%;
    left: 50%;
    transform: translateX(-50%);
    width: 20%;
    height: 2px;
    background-color: rgb(255, 255, 255);
    clip-path: polygon(0% 0%, 0 0, 0 100%, 0% 100%);
}
.loading-percentage{
    position: absolute;
    bottom: 25%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
}

/* Navbar */
.nav {
    opacity: 0;
    position: absolute;
    top: 5%;
    left: 5%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    z-index: 1;
    color: white;
}
.nav a,li{
    text-decoration: none;
    color: white;
    font-family: 'Varela', sans-serif;
    list-style-type: none;
    margin-right: 15px;
    cursor: pointer;
}
.nav li:hover, .nav a:hover{
    color: rgb(0, 0, 0);
}
#nav-name{
    font-family: 'Varela', sans-serif;
    color: var(--g);
    font-weight: 900;
}
/* Socials */
.socials {
    position: absolute;
    top: 10%;
    left: 5%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    z-index: 1;
}
.socials-links {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    /* border: 1px solid  var(--g); */
    border-radius: 50%;
    margin-right:15px;
    color:rgb(255, 255, 255);
    cursor: pointer;
    transition: color 0.2s;
    will-change: color;
}
.socials-links:hover{
    background: white;
    color: black;
}


/* Sidebar */
.sidebar {
    opacity: 0;
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 30px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
    z-index:10;
}
.sidebar-line {
    position: relative;
    width: 100%;
    height: 2px;
    background: white;
}
.sidebar:hover .sidebar-line{
    background: black;
}
.sidebar-line::before{
    content: '';
    position: absolute;
    top: 8px;
    left: 0;
    width: 80%;
    height: 2px;
    background: white;
    transition: all 0.2s;
}
.sidebar-line::after{
    content: '';
    position: absolute;
    top: -8px;
    left: 0;
    width: 60%;
    height: 2px;
    background: white;
    transition: all 0.2s;
}
.sidebar:hover .sidebar-line::before{
    width: 100%;
    transform: translateX(20%);
    background: black;
}
.sidebar:hover .sidebar-line::after{
    width: 100%;
    transform: translateX(20%);
    background: black;
}
.menu{
    height: 100vh;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0);
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    clip-path: polygon(0% 100%, 100% 100%, 100% 100%,0% 100%);
}
.menu-links{
    text-decoration: none;
    position: relative;
    font-size: 20px;
    color: white;
    margin: 15px;
    cursor: pointer;
}
.menu-links::after{
    position: absolute;
    bottom: -5px;
    left: 0;
    content: '';
    width: 100%;
    height: 1px;
    background-color: var(--g);
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: clip-path 0.1s;
    will-change: clip-path;
}
.menu-links:hover::after{
    clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
}

.open-menu{
    transform: translateX(0);
}
.close-menu-btn{
    position: absolute;
    top: 5%;
    right: 5%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.close-menu-btn div{
    width: 100%;
    height: 3px;
    background: rgb(49, 49, 49);
    border-radius: 15px;
}
.close-menu-btn div::after{
    content:'';
    position: absolute;
    left: -1px;
    top: 20px;
    width: 18px;
    height: 3px;
    background: rgb(49, 49, 49);
    transform: rotate(155deg);
    border-radius: 15px;
}

/* Progress bar */
.progress{
    opacity: 0;
    position: fixed;
    right: 5%;
    bottom: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    z-index: 2;
}
.progress-text {
    color: white;
}
.progress-div{
    margin: 3px 0;
    width: 30px;
    height: 1px;
    background: rgb(255, 255, 255);
    transition: height 0.2s, background 0.2s;
}
#progress-0 {
    width: 50px;
}
#progress-1 {
    width: 45px;
}
#progress-2 {
    width: 40px;
}
#progress-3 {
    width: 35px;
}
#progress-4 {
    width: 30px;
}
#progress-5 {
    width: 25px;
}

/* Contact page*/
.contact{
    background: rgb(0, 0, 0);
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact-text {
    color: white;
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.contact-text h1 {
    position: relative;
    font-size: 3rem;
}
.contact-text h5{
    margin: 0.5em 0;
    color: rgb(228, 228, 228);
}
.contact-text p {
    margin: 0.5em 0;
}
.contact-form {
    width: 40%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;

}
#contact-form{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
#contact-form label{
    color: white;
}
.name-div {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.name-div div{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
#fname,#lname{
    padding: 10px;
    font-size: 0.8rem;
    color: white;
    border: none;
    width:95%;
    height: 30px;
    background: rgb(26, 26, 26);
    margin: 10px 0 20px 0;
}
#email {
    padding: 10px;
    font-size: 0.8rem;
    color: white;
    border: none;
    width: 98%;
    height: 30px;
    margin: 10px 0 20px 0;
    background: rgb(26, 26, 26);
}
#fname:focus, #lname:focus, #email:focus{
    outline: 2px solid var(--g);
}
#contact-form textarea {
    border: none;
    padding: 10px;
    font-size: 0.8rem;
    color: white;
    width: 98%;
    resize: none;
    height: 100px;
    margin: 10px 0 20px 0;
    background: rgb(26, 26, 26);
}
#contact-form textarea:focus{
    outline: 2px solid var(--g);
}
.contact-btn {
    width: 98%;
    height: 40px;
    border: none;
    background: rgb(26, 26, 26);
    color: white;
    cursor: pointer;
    transition: all 0.2s;
}
.contact-btn:hover {
    background: rgb(143, 143, 143);
}

/* Projects Page */
.projects-showcase{
    opacity: 0;
    transform: scaleX(0);
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(201, 201, 201, 0);
    z-index: 10;
}
.projects-showcase-prev-btn{
    color: white;
    font-weight: 500;
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
}
.projects-showcase-prev-btn::after{
    position: absolute;
    bottom: -5px;
    left: 0;
    content: '';
    width: 100%;
    height: 1px;
    background-color: var(--g);
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: clip-path 0.1s;
    will-change: clip-path;
}
.projects-showcase-prev-btn:hover::after{
    clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
}
.projects-showcase-next-btn{
    color: white;
    border: none;
    font-weight: 500;
    position: absolute;
    right: 10%;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0);
    cursor: pointer;
}
.projects-showcase-next-btn::after{
    position: absolute;
    bottom: -5px;
    left: 0;
    content: '';
    width: 100%;
    height: 1px;
    background-color: var(--g);
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: clip-path 0.1s;
    will-change: clip-path;
}
.projects-showcase-next-btn:hover::after{
    clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
}
.projects-showcase-title{
    width: 100%;
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translate(-50%,-5%);
    font-family: 'Raleway', sans-serif;
    letter-spacing: 2px;
    list-style-type: none;
    font-size: 4rem;
    font-weight: 900;
    text-align: center;
    text-transform: uppercase;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    color: rgba(0, 0, 0, 0);
    pointer-events: none;
}
.projects-showcase-link{
    text-decoration: none;
    color: white;
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translate(-50%,-15%);
    cursor: pointer;
    transition: all 0.2s;
}
.projects-showcase-link:hover{
    color: white;
    font-weight: 600;
}
.projects-showcase-desc{
    position: absolute;
    top: 15%;
    left: 50%;
    transform: translateX(-50%);
    width: 50%;
    height: 30%;
    padding: 15px;
    background: rgba(0, 0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
.projects-showcase-desc p{
    margin: 5px 0;
    color: white;
    font-size: 13px;
    text-align: center;
    line-height: 1.5em;
    pointer-events: none;
}
.projects-showcase-desc div{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    pointer-events: none;
}
.projects-showcase-desc span{
    margin: 5px 0;
    color: rgb(148, 148, 148);
    font-size: 14px;
    text-align: center;
}
.close-projects-btn{
    position: relative;
    width: 20px;
    height: 15px;
    opacity: 0;
    cursor: pointer;
    transition: transform 0.2s;
    will-change: transform;
}
.close-projects-btn::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    top: 3px;
    left: 0;
    background: rgb(255, 255, 255);
    transform: rotate(45deg);
}
.close-projects-btn::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 10px;
    left: 0;
    background: rgb(255, 255, 255);
    transform: rotate(-45deg);
}
.close-projects-btn:hover{
    transform: translateX(5px);
}
