/* Skills page */
.skills-page{
    background: black;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
}
.skills-page-content{
    width: 40%;
    height: auto;
    margin: 50px 0;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.skills-page-content h1{
    font-size: 2rem;
}
.skills-page-content p{
    color: rgb(196, 196, 196);
    margin-bottom: 50px;
}
.skills-page-content span{
    color: white;
    font-weight: 600;
    margin-right: 5px;
}
.skills-btn{
    background: black;
    border: none;
    color: white;
    font-weight: 600;
    font-size: 16px;
    text-decoration: underline;
    cursor: pointer;
}
.skills-btn:hover{
    color: grey;
}

/* About page */
.about-page{
    width: 100%;
    height: 100vh;
    background: black;
    position: relative;
}
.about-left{
    position: absolute;
    left: 10%;
    top: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 15px;
    color: white;
    width: 30%;
    height: 50%;
}
.about-left p, .about-right p{
    color: rgb(201, 201, 201);
    line-height: 1.5em;
    font-size: 13px;
}
.about-right{
    position: absolute;
    left: 10%;
    bottom: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 15px;
    color: white;
    width: 30%;
    height: 50%;
}
.about-left h1, .about-right h1{
    font-family: 'Raleway', sans-serif;
    letter-spacing: 2px;
    line-height: 0.9em;
    list-style-type: none;
    font-size: 3rem;
    font-weight: 900;
    text-transform: uppercase;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    color: rgba(0, 0, 0, 0);
    margin-bottom: 15px;
}

/* Media queries */
@media only screen and (max-width: 480px){
    .nav{
        top: 10%;
        left: 0;
        width: 100%;
        align-items: center;
        justify-content: center;
    }
    .contact-text{
        width: 90%;
    }
    .contact-form{
        width: 90%;
    }
    .skills-page-content{
        width: 80%;
    }
    .projects-showcase-prev-btn{
        left: 5%;
    }
    .projects-showcase-next-btn{
        right: 5%;
    }
    .projects-showcase-desc{
        width: 100%;
    }
}
